import resourceObj from './resource';


export default {
  //
  getOrgTreeList: (data) => {
    return resourceObj.orgTreeResource.get({id:'queryOrganizationalStructure'});
  },
  //
  getChargePersonList: (data) => {
    return resourceObj.orgResource.save({
      id: 'managers'
    }, data)
  },
  //POST /struct/setmanager
  saveChargePersonList: (data) => {
    return resourceObj.orgResource.save({
      id: 'setmanager'
    }, data)
  },
  //
  getChargeLeaderList: (data) => {
    return resourceObj.orgResource.save({
      id: 'leader'
    }, data)
  },
  //POST /struct/setmanager
  saveChargeLeaderList: (data) => {
    return resourceObj.orgResource.save({
      id: 'setLeader'
    }, data)
  },
  //POST /struct/createcompany
  addCompany: (data) => {
    return resourceObj.orgResource.save({
      id: 'createcompany'
    }, data)
  },
  //POST /struct/createdepartment
  addDepartment: (data) => {
    return resourceObj.orgResource.save({
      id: 'createdepartment'
    }, data)
  },
  ///POST /struct/changeorgname
  editOrgName: (data) => {
    return resourceObj.orgResource.save({
      id: 'changeorgname'
    }, data)
  },
  ///
  deleteOrg: (data) => {
    return resourceObj.orgResource.get({
      id: 'delete',
      orgId: data.orgId,
      type: data.type
    })
  },


  // 
  userBaseList: (data) => {
    return resourceObj.userbaseResource.save({
      id: 'queryUserByDepartmentIdList'
    }, data)
  },

  //
  userUpdate: (data) => {
    return resourceObj.adduserResource.save({
      id: 'status',
      val: data.id,
      status: data.status,
      account: data.userName
    })
  },

  //
  getUserDetail: (data) => {
    return resourceObj.userResource.get({
      id: 'get',
      val: data
    })
  },
  //
  addUser: (data) => {
    return resourceObj.orgUser.save({
      id: 'editUserRolePrivilege'
    }, data)
  },


}



