                <!-- <el-form-item label=":" prop="uCompanyId" v-show='companyType==2'>
                  <el-select v-model="selectedOptions" :filterable="true" :remote-method="searchCompany" remote clearable @change="handleChange" placeholder="" style="width: 100%;">
                    <el-option :key="value.id" v-for="value in selectList" :label="value.cName" :value="value.id">
                    </el-option>
                  </el-select>
                </el-form-item> -->
                <el-form-item label=":">
                      <el-checkbox-group
