import resourceObj from './resource';


export default {
  //
  getOrgTreeList: (data) => {
    if(process.env.NODE_ENV==='development'){
      return resourceObj.orgTreeResource.get({id:'queryOrganizationalStructure',companyId:data})
    }else{
      return resourceObj.orgTreeResource.get({
        id: 'queryOrganizationalStructure'
      })      
    }
  },
  //
  getChargePersonList: (data) => {
    return resourceObj.orgResource.save({
      id: 'managers'
    }, data)
  },
  //POST /struct/setmanager
  saveChargePersonList: (data) => {
    return resourceObj.orgResource.save({
      id: 'setmanager'
    }, data)
  },
  //POST /struct/createcompany
  addCompany: (data) => {
    return resourceObj.orgResource.save({
      id: 'createcompany'
    }, data)
  },
  //POST /struct/createdepartment
  addDepartment: (data) => {
    return resourceObj.orgResource.save({
      id: 'createdepartment'
    }, data)
  },
  ///POST /struct/changeorgname
  editOrgName: (data) => {
    return resourceObj.orgResource.save({
      id: 'changeorgname'
    }, data)
  },
  ///
  deleteOrg: (data) => {
    return resourceObj.orgResource.get({
      id: 'delete',
      orgId: data.orgId,
      type: data.type
    })
  },
  // 
  getRoleList: (data) => {
    return resourceObj.roleResource.save({
      id: 'list'
    }, data)
  },
  // 
  roleUpdate: (data) => {
    return resourceObj.roleResource.save({
      id: 'modifyRoleModule'
    }, data)
  },
  // 
  roleEdit: (data) => {
    return resourceObj.roleResource.save({
      id: 'queryRoleModule'
    }, data)
  },
  // 
  roleRemove: (data) => {
    return resourceObj.roleResource.save({
      id: 'removeRole'
    }, data)
  },
  // 
  userBaseList: (data) => {
    return resourceObj.userbaseResource.save({
      id: 'queryUserByDepartmentIdList'
    }, data)
  },

  //
  userUpdate: (data) => {
    return resourceObj.adduserResource.save({
      id: 'status',
      val: data.id,
      status: data.status,
      account: data.userName
    })
  },

  //
  getUserDetail: (data) => {
    return resourceObj.userResource.get({
      id: 'get',
      val: data
    })
  },
  //
  addUser: (data) => {
    return resourceObj.orgUser.save({
      id: 'editUserRolePrivilege'
    }, data)
  },
  //
  queryRoleList: (data) => {
    return resourceObj.orgTreeResource.get({
      id: 'queryRoleListByUserId'
    }, data)
  },
  //
  getAuthenticDetail: (data) => {
    return resourceObj.orgTreeResource.get({
      id: 'queryUserRolePrivilegeByUserId',
      userId: data.userId,
      type: data.type
    })
  },

}



